import React, { useEffect, useState, useCallback } from "react";
import CustomTable from "@/Shared/Table/CustomTable";
import { useElderlyTableColumns } from "./Utiles/utiles2";
import { SidebarContext } from "@/Context/CustomUsertable";
import { Segmented, ConfigProvider, Select } from "antd";
import { getElderlies } from "@/api/elderly";
import SearchInput from "@/Shared/Search/SearchInput";

export default function ElderlyList() {
  const [search, setSearch] = useState("");
  const [elderlies, setElderlies] = useState([]);
  const [loading, setLoading] = useState(false);
  const columns = useElderlyTableColumns("support_agent");
  const [elderliesQuery, setElderliesQuery] = useState({});
  const getElderlyList = useCallback(() => {
    setLoading(true);
    getElderlies({ ...elderliesQuery, name: search })
      .then((res) => {
        setElderlies(res);
        setLoading(false);
      })
      .catch((err) => {
        console.log(err);
        setLoading(false);
      });
  }, [elderliesQuery, search]);
  useEffect(() => {
    getElderlyList();
  }, [getElderlyList]);
  function hnadleChangeElderlyFilter(data) {
    switch (data) {
      case "diseases":
        return setElderliesQuery({ diseases: true });
      case "comments":
        return setElderliesQuery({ comments: true });

      case "medications":
        return setElderliesQuery({ medications: true });
      default:
        return setElderliesQuery({});
    }
  }
  return (
    <>
      <div
        id="Elderlies"
        className="w-full flex flex-col gap-4 justify-center items-center bg-white rounded-2xl p-6 mt-8"
      >
        <div id="Elderlies_Header" className="w-full flex justify-between">
          <h1 className="text-[24px] font-bold">Users</h1>
          <div className="flex gap-4">
            {/* <ConfigProvider
              theme={{
                token: {
                  fontFamily: "Baloo2",
                  colorPrimary: "#8086AC",
                  colorLinkActive: "#8086AC",
                  colorLinkHover: "#8086AC",
                  colorLink: "#8086AC",
                },
              }}
            >
              <Select
                size="large"
                options={elderlyListSelectOptions}
                defaultValue={elderlyListSelectOptions[0].value}
                onChange={(value) => hnadleChangeElderlyFilter(value)}
                className="min-w-[160px]"
              />
            </ConfigProvider> */}
            <SearchInput
              search={search}
              setSearch={setSearch}
              placeholder="Search Users"
            />
          </div>
        </div>
        <SidebarContext.Provider
          value={{
            total: elderlies?.data?.length,
            page: 1,
            SetPage: () => {},
          }}
        >
          <CustomTable
            loading={loading}
            columns={columns}
            tableData={elderlies?.data}
          />
        </SidebarContext.Provider>
      </div>
    </>
  );
}
export const elderlyListSegmentOptions = [
  "All Elderlies",
  "With Alerts",
  "With Diseases",
  "With Allergies",
  "Taking Medications",
];
export const elderlyListSelectOptions = [
  { label: "All Users", value: "all" },
  { label: "With Sensitivity Factors", value: "diseases" },
  { label: "With Comments", value: "comments" },
];
